var navbar = document.getElementById('navbar');
var header = document.getElementById('header');
var content = document.getElementById('content');
export var links = {
    navbar: navbarLinks,
    content: contentLinks,
};
function navbarLinks() {
    return Array.from(navbar.getElementsByClassName('nav-link')).concat(Array.from(header.getElementsByClassName('nav-link')));
}
function contentLinks() {
    return Array.from(content.getElementsByClassName('nav-link'));
}
export var cartButtons = {
    details: [],
    addToCart: [],
    removeFromCart: [],
    deleteFromCart: []
};
export function getCartBtns() {
    cartButtons.details = Array.from(content.getElementsByClassName('js-product-detail'));
    cartButtons.addToCart = Array.from(content.getElementsByClassName('js-add'));
    cartButtons.removeFromCart = Array.from(content.getElementsByClassName('js-remove'));
    cartButtons.deleteFromCart = Array.from(content.getElementsByClassName('js-delete'));
}
